/* Agregar estilos CSS para el botn en el efecto hover */
.btn-purple {
  background-color: purple; /* Color morado */
  border-color: purple; /* Borde morado */
  border-radius: 1rem;
  margin: 1rem;
}

.btn-purple:hover {
  background-color: #8a2be2; /* Color prpura ms claro en el hover */
  border-color: #8a2be2; /* Borde prpura ms claro en el hover */
}
